




















































































import Vue from 'vue';
import axios from 'axios';
import { Platform } from 'typings/interface/platform';
import STATUS_COLORS from '@/constants/statusColors';
import RepositoryFactory from '@/api/RepositoryFactory';

const PlatformsRepository = RepositoryFactory.get('platforms');

export default Vue.extend({
  props: {
    container: {
      type: Object,
      required: true,
    },
  },
  data:() => ({
    platform: {} as Platform,
    statusColors: STATUS_COLORS,
    isLoading: false,
  }),
  watch: {
    container() {
      const platformId = this.container.platform;
      this.getPlatform(platformId);
    },
  },
  mounted(): void {
    const platformId = this.container.platform;
    this.getPlatform(platformId);
  },
  methods: {
    async getPlatform(platformId: number) {
      this.isLoading = true;
      const response = await PlatformsRepository.getPlatform(platformId);
      this.platform = response.data;
      this.isLoading = false;
    },
  },
});
