import PlatformsRepository from './platformsRepository';
import ConttainersRepository from './containersRepository';
import EmittersRepository from './emittersRepository';
import LoginRepository from './loginRepository';

type Name = {
  [key: string]: any,
};

const repositories: Name = {
  login: LoginRepository,
  platforms: PlatformsRepository,
  containers: ConttainersRepository,
  emitters: EmittersRepository,
};

const RepositoryFactory = {
  get: (name: string) => repositories[name],
};

export default RepositoryFactory;
