import Repository from './Repository';

const resource = '/containers';
export default {
  get(pageNumber: number) {
    return Repository.get(`${resource}/?page=${pageNumber}`);
  },
  getContainer(containerId: number, fields: Array<string>) {
    let repository;

    if (fields) {
      const fieldsInline = fields.join(',');
      repository = Repository.get(`${resource}/${containerId}/?=${fieldsInline}`);
    } else {
      repository = Repository.get(`${resource}/${containerId}/`);
    } return repository;
  },
};
