






































import Vue from 'vue';
import LogoutButton from '@/components/LogoutButton.vue';

export default Vue.extend({
  name: 'App',
  components: {
    LogoutButton,
  },
  data: () => ({
    appTopHeight: 0,
    isAuthenticated: false,
    drawer: false,
    group: null,
  }),
  watch: {
    group() {
      this.drawer = false;
    },
  },
  created() {
    this.appTopHeight = this.$vuetify.application.top;
  },
});
