


































// Imports
import Vue from 'vue';
import AUTH from '@/store/actions/auth';

export default Vue.extend({
  data:() => ({
    email: '' as string,
    password: '' as string,
  }),
  methods: {
    login() {
      const { password, email } = this;
      this.$store.dispatch(AUTH.REQUEST, { password, email })
        .then(() => {
          this.$router.push('/');
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
});
