























































































// Imports
import Vue from 'vue';
import axios from 'axios';
import { Container } from 'typings/interface/container';
import { tableSticky } from '@/components/mixins/tableSticky';
import CONTAINER_STATUSES from '@/constants/containerStatuses';
import STATUS_COLORS from '@/constants/statusColors';
import CONTAINER_STATUSES_DICT from '@/dictionaries/containerStatuses';
import ExpandedContent from './ExpandedContent.vue';
import RepositoryFactory from '@/api/RepositoryFactory';

const ContainersRepository = RepositoryFactory.get('containers');

export default Vue.extend({
  components: {
    ExpandedContent,
  },
  mixins: [tableSticky],
  data:() => ({
    tableSticky_expanded: [],
    filters: {},
    selected: [],
    headers: [
      {
        text: 'Код',
        value: 'id',
        sortable: false,
      },
      {
        text: 'Тип по плану',
        value: 'type_plan',
        sortable: false,
      },
      {
        text: 'Статус',
        value: 'status',
        sortable: false,
      },
    //   {
    //     text: 'Лот',
    //     value: 'lot',
    //     sortable: false,
    //   },
    //   {
    //     text: 'Адрес площадки',
    //     value: 'name',
    //     sortable: false,
    //   },
      // {
      //   text: 'Собственник',
      //   value: 'person',
      //   sortable: false,
      // },
      // {
      //   text: 'Объём (План.)',
      //   value: 'volumePlan',
      //   sortable: false,
      // },
      // {
      //   text: 'Объём (Факт.)',
      //   value: 'volumeFact',
      //   sortable: false,
      // },
    ],
    containers: [] as Array<Container>,
    containerStatusesDict: CONTAINER_STATUSES_DICT,
    containerStatuses: CONTAINER_STATUSES,
    statusColors: STATUS_COLORS,
    pageCount: 1,
    itemsPerPage: 0,
    isLoading: true,
  }),
  computed: {
    currentItem() {
      const [currentItem] = this.tableSticky_expanded;
      return currentItem;
    },
  },
  mounted(): void {
    const page = this.pageCount;
    this.getContainers(page);
  },
  methods: {
    getRowValue(item: never): void {
      const [expItem] = this.tableSticky_expanded;
      this.tableSticky_expanded = this.tableSticky_expanded.includes(item) ? [] : [item];
    },
    async getContainers(pageNumber: number) {
      this.isLoading = true;
      const response = await ContainersRepository.get(pageNumber);
      this.containers = response.data.results;
      this.pageCount = response.data.count;
      this.itemsPerPage = this.containers.length;
      this.isLoading = false;
    },
  },
});
