import Repository from './Repository';

const resource = '/emitters';
const available = 'available';
const assignToContainer  = 'assign-to-container';
export default {
  get(platformId: number, containerId: number) {
    return Repository.get(`${resource}/${available}/${platformId}/${containerId}/`);
  },
  patch(containerId: number, emitterId: number) {
    return Repository.patch(`${resource}/${assignToContainer}/${containerId}/${emitterId}/`);
  },
  delete(containerId: number, emitterId: number) {
    return Repository.delete(`${resource}/${assignToContainer}/${containerId}/${emitterId}/`);
  },
};
