import Vue from 'vue';

export const tableSticky = Vue.extend({
  data:() => ({
    tableSticky_expanded: [],
    tableSticky_scrollPosition: 0,
    tableSticky_rowHeight: 0,
    tableSticky_isInsideStickyBorder: false,
    tableSticky_isEntryStickyBorder: false,
  }),
  mounted() {
    this.tableSticky_initTableSticky();
  },
  methods: {
    tableSticky_initTableSticky(): void {
      const [tableWrapper] = this.$el.getElementsByClassName('v-data-table__wrapper');

      this.tableSticky_initEvents(tableWrapper);
    },
    tableSticky_initEvents(twrap: any): void {
      if (twrap) {
        const [thead] = twrap.getElementsByTagName('thead');

        twrap.addEventListener('scroll', () => {
          this.tableSticky_setValueStickyBorder(twrap);

          // Call functions when scroll position is entry into sticky border
          this.tableSticky_entryStickyBorder(twrap);

          // Call functions when scroll position is leave into sticky border
          this.tableSticky_leaveStickyBorder(twrap);
        });
        twrap.addEventListener('click', (e: any) => {
          this.tableSticky_scrollToClickedRow(e, thead, twrap);
        });
      }
    },
    tableSticky_scrollToClickedRow(e: any, thead: any, twrap: any): void {
      const elementTarget = e.target;
      const trow = elementTarget.closest('.v-data-table__expanded__row');

      if (this.isExpandOpened && trow) {
        const theadHeight = thead.clientHeight;

        this.tableSticky_scrollPosition = trow.offsetTop - theadHeight;
        this.tableSticky_rowHeight = trow.clientHeight;
        twrap.scrollTo(0, this.tableSticky_scrollPosition);
      }
    },
    tableSticky_stickToClickedRow(twrap: any): void {
      if (this.isExpandOpened) {
        twrap.scrollTo(0, this.tableSticky_scrollPosition);
      }
    },
    tableSticky_setValueStickyBorder(twrap: any): void {
      const twrapScroll = {
        isOver: false,
        isUnder: false,
        isInside: false,
        position: twrap.scrollTop,
      };
      const BORDER_SIZE = 15;
      const borderStick = {
        top: this.tableSticky_scrollPosition - BORDER_SIZE,
        bottom: this.tableSticky_scrollPosition + this.tableSticky_rowHeight + BORDER_SIZE,
      };
      twrapScroll.isOver = twrapScroll.position >= borderStick.top;
      twrapScroll.isUnder = twrapScroll.position <= borderStick.bottom;
      twrapScroll.isInside = twrapScroll.isOver && twrapScroll.isUnder;

      this.tableSticky_isInsideStickyBorder = twrapScroll.isInside;
    },
    tableSticky_entryStickyBorder(twrap: any) {
      if (this.tableSticky_isInsideStickyBorder && !this.tableSticky_isEntryStickyBorder) {
        this.tableSticky_isEntryStickyBorder = true;

        this.tableSticky_stickToClickedRow(twrap);

        if (this.isExpandOpened) {
          // this.initMapHeight_toggleMapHeight();
        }
      }
    },
    tableSticky_leaveStickyBorder(twrap: any) {
      if (!this.tableSticky_isInsideStickyBorder && this.tableSticky_isEntryStickyBorder) {
        this.tableSticky_isEntryStickyBorder = false;
      }
    },
    isExpandOpened(): boolean {
      return Boolean(this.tableSticky_expanded.length);
    },
  },
});

export default tableSticky;
