









import Vue from 'vue';
import CardPlatformDetailView from '@/components/CardPlatform/DetailView.vue';
import CardContainerDetailView from '@/components/CardContainer/DetailView.vue';

export default Vue.extend({
  components: {
    CardPlatformDetailView,
    CardContainerDetailView,
  },
  data:() => ({
    platformId: null as any,
    containerId: null as any,
  }),
  watch: {
    $route(newRoute) {
      this.updateContainerId(newRoute);
    },
  },
  created() {
    const route = this.$route;
    this.platformId = Number(this.$route.params.id);
    this.updateContainerId(route);
  },
  methods: {
    updateContainerId(route: any): void {
      this.containerId = Number(route.params.containerId);
    },
  },
});
