



































































































import Vue from 'vue';
import axios from 'axios';
import { Container } from 'typings/interface/container';
import STATUS_COLORS from '@/constants/statusColors';
import RepositoryFactory from '@/api/RepositoryFactory';

const PlatformsRepository = RepositoryFactory.get('platforms');

export default Vue.extend({
  props: {
    platform: {
      type: Object,
      required: true,
    },
  },
  data:() => ({
    containers: [] as Array<Container>,
    statusColors: STATUS_COLORS,
    cardWidth: 0,
    isLoading: false,
    currentPlatform: false,
  }),
  watch: {
    platform(): void {
      const platformId = this.platform.id;
      const fields = ['containers'];
      this.getPlatform(platformId, fields);
    },
  },
  mounted(): void {
    const platformId = this.platform.id;
    const fields = ['containers'];
    this.getPlatform(platformId, fields);
  },
  methods: {
    async getPlatform(platformId: number, fields: Array<string>) {
      this.isLoading = true;
      const response = await PlatformsRepository.getPlatform(platformId, fields);
      this.containers = response.data.containers;
      this.isLoading = false;
    },
  },
});
