





















// Import
import Vue from 'vue';
import { yandexMap, ymapMarker } from 'vue-yandex-maps';

export default Vue.extend({
  components: {
    yandexMap,
    ymapMarker,
  },
  props: {
    mapItem: {
      type: Object,
      default: null,
    },
    mapHeight: {
      type: Number,
      default: 340,
    },
  },
  data:() => ({
    mapSettings: {
      // apiKey: '52a7b0c7-e889-4f69-930f-1c78aacede59',
      lang: 'ru_RU',
      coordorder: 'latlong',
      version: '2.1',
    },
    mapInit: false,
    mapCoords: [-1, -1] as any,
  }),
  computed: {
    mapConfiguration() {
      const coords = this.mapCoords;
      const markerId = this.mapItem ? this.mapItem.id : -1;
      return {
        zoom: 17,
        coords,
        markerId,
        controls: [
          'fullscreenControl',
          'geolocationControl',
          'zoomControl',
        ],
      };
    },
    cssVars() {
      return {
        '--mapHeight': `${this.mapHeight}px`,
      };
    },
  },
  watch: {
    mapItem() {
      if (this.mapItem) {
        this.mapCoords = [
          this.mapItem.point.latitude,
          this.mapItem.point.longitude,
        ];
      }
    },
  },
  methods: {
    mapWasInitialized(): void {
      this.mapInit = true;
    },
  },
});
