import Repository from './Repository';

const resource = '/platforms';
export default {
  get(pageNumber: number) {
    return Repository.get(`${resource}/?page=${pageNumber}`);
  },
  getPlatform(platformId: number, fields: Array<string>) {
    let repository;

    if (fields) {
      const fieldsInline = fields.join(',');
      repository = Repository.get(`${resource}/${platformId}/?=${fieldsInline}`);
    } else {
      repository = Repository.get(`${resource}/${platformId}/`);
    } return repository;
  },
};
