
























































































































// Imports
import Vue from 'vue';
import { Platform } from 'typings/interface/platform';
import { tableSticky } from '@/components/mixins/tableSticky';
import ExpandedContent from './ExpandedContent.vue';
import EcotecMap from '@/components/EcotecMap.vue';
import RepositoryFactory from '@/api/RepositoryFactory';

const PlatformsRepository = RepositoryFactory.get('platforms');

export default Vue.extend({
  components: {
    ExpandedContent,
    EcotecMap,
  },
  mixins: [tableSticky],
  data:() => ({
    tableSticky_expanded: [],
    filters: {},
    selected: [],
    headers: [
      {
        text: 'Код',
        value: 'id',
        sortable: false,
      },
      {
        text: 'Адрес',
        value: 'name',
        sortable: false,
      },
      {
        text: 'Лот',
        value: 'lot',
        sortable: false,
      },
      {
        text: 'Количество контейнеров (Факт.)',
        value: 'count_fact',
        sortable: false,
      },
      // {
      //   text: 'Количество контейнеров (План.)',
      //   value: 'containerCountsPlan',
      //   sortable: false,
      // },
      // {
      //   text: 'Объём (План.)',
      //   value: 'volumePlan',
      //   sortable: false,
      // },
      // {
      //   text: 'Объём (Факт.)',
      //   value: 'volumeFact',
      //   sortable: false,
      // },
    ],
    platforms: [] as Array<Platform>,
    pageCount: 1,
    itemsPerPage: 0,
    isLoading: false,
  }),
  computed: {
    currentItem() {
      const [currentItem] = this.tableSticky_expanded;
      return currentItem;
    },
  },
  mounted(): void {
    const page = this.pageCount;
    this.getPlatforms(page);
  },
  methods: {
    getRowValue(item: never): void {
      const [expItem] = this.tableSticky_expanded;
      this.tableSticky_expanded = this.tableSticky_expanded.includes(item) ? [] : [item];
    },
    async getPlatforms(pageNumber: number) {
      this.isLoading = true;
      const response = await PlatformsRepository.get(pageNumber);
      this.platforms = response.data.results;
      this.pageCount = response.data.count;
      this.itemsPerPage = this.platforms.length;
      this.isLoading = false;
    },
  },
});
