

























































































































import Vue from 'vue';
import axios from 'axios';
import { Container } from 'typings/interface/container';
import { Emitter } from 'typings/interface/emitter';
import CONTAINER_STATUSES from '@/constants/containerStatuses';
import STATUS_COLORS from '@/constants/statusColors';
import RepositoryFactory from '@/api/RepositoryFactory';

const ContainersRepository = RepositoryFactory.get('containers');
const EmittersRepository = RepositoryFactory.get('emitters');

export default Vue.extend({
  props: {
    containerId: {
      type: Number,
      required: true,
    },
    platformId: {
      type: Number,
      required: true,
    },
  },
  data:() => ({
    container: {} as Container,
    emitters: [] as Array<Emitter>,
    statusColors: STATUS_COLORS,
    containerStatuses: CONTAINER_STATUSES,
    selectedEmitters: [],
    dialog: false,
    loading: false,
    isLoading: false,
  }),
  watch: {
    containerId() {
      const id = this.containerId;
      this.getContainer(id);
    },
  },
  mounted(): void {
    const id = this.containerId;
    this.getContainer(id);
  },
  methods: {
    async getContainer(containerId: number) {
      this.isLoading = true;
      const response = await ContainersRepository.getContainer(containerId);
      this.container = response.data;
      this.isLoading = false;
    },
    async getPlatformEmitters(platformId: number, containerId: number) {
      this.loading = true;
      const response = await EmittersRepository.get(platformId, containerId);
      this.emitters = response.data;
      this.loading = false;
    },
    async patchEmitter(containerId: number, emitterId: number) {
      await EmittersRepository.patch(containerId, emitterId);
    },
    async deleteEmitter(containerId: number, emitterId: number) {
      await EmittersRepository.delete(containerId, emitterId);
    },
    async getContainerEmitters(containerId: number) {
      const response = await ContainersRepository.getContainer(containerId);
      this.$set(this.container, 'emitters', response.data.emitters);
    },
    patchEmittersWithUpdate(platformId: number, containerId: number, selectedEmitters: any) {
      if (selectedEmitters.length) {
        const promises = [] as any;
        selectedEmitters.forEach((emitterId: number) => {
          promises.push(this.patchEmitter(containerId, emitterId));
        });
        Promise.all(promises).then(
          () => {
            this.getPlatformEmitters(platformId, containerId);
            this.getContainerEmitters(containerId);
            this.dialog = false;
          },
        );
      }
    },
    deleteEmitterWithUpdate(containerId: number, emitterId: number) {
      this.deleteEmitter(containerId, emitterId);
      this.getContainerEmitters(containerId);
    },
  },
});
