import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/views/authorization/Login.vue';
import Platforms from '@/views/platforms/Platforms.vue';
import Containers from '@/views/containers/Containers.vue';
import Detail from '@/views/Detail.vue';
import store from '@/store';

Vue.use(Router);

const ifNotAuthenticated = (to: any, from: any, next: any) => {
  if (!store.getters.isAuthenticated) {
    next();
  } else {
    next('/');
  }
};

const ifAuthenticated = (to: any, from: any, next: any) => {
  if (store.getters.isAuthenticated) {
    next();
  } else {
    next('/login');
  }
};

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/platforms',
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      beforeEnter: ifNotAuthenticated,
    },
    {
      path: '/platforms',
      name: 'platforms',
      component: Platforms,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/platform/:id',
      name: 'platform',
      component: Detail,
      beforeEnter: ifAuthenticated,
      children: [
        {
          path: ':containerId',
          name: 'container',
          component: Detail,
          beforeEnter: ifAuthenticated,
        },
      ],
    },
    {
      path: '/containers',
      name: 'containers',
      component: Containers,
      beforeEnter: ifAuthenticated,
    },
  ],
});
